import * as React from 'react';
import { useState, useEffect } from 'react';
import { navigate } from 'gatsby'; // Import navigate from Gatsby
import Layout from '../components/Layout';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

// Manual slugify function
const slugify = (str) => {
  return str
    .toLowerCase() // Convert to lowercase
    .trim() // Remove leading and trailing spaces
    .replace(/[^\w\s-]/g, '') // Remove special characters
    .replace(/[\s_-]+/g, '-') // Replace spaces and underscores with hyphens
    .replace(/^-+|-+$/g, ''); // Remove leading and trailing hyphens
};

const IndexPage = () => {
  const [text, setText] = useState('');
  const [appName, setAppName] = useState(''); // State to store the app name
  const fullText = 'Create your app today';
  const [index, setIndex] = useState(0);

  // Typing animation effect
  useEffect(() => {
    if (index < fullText.length) {
      const timeout = setTimeout(() => {
        setText((prevText) => prevText + fullText[index]);
        setIndex((prevIndex) => prevIndex + 1);
      }, 100); // Adjust typing speed here (100ms per character)

      return () => clearTimeout(timeout);
    }
  }, [index, fullText]);

  // Handle Start button click
  const handleStart = () => {
    if (appName.trim() === '') {
      alert('Please enter an app name.'); // Validate input
      return;
    }

    // Slugify the app name using the manual function
    const slug = slugify(appName);

    // Redirect to the new route
    navigate(`/app/${slug}`);
  };

  return (
    <Layout>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          minHeight: '80vh',
          textAlign: 'center',
        }}
      >
        {/* Heading with Typing Animation */}
        <Typography
          variant="h3"
          component="h1"
          gutterBottom
          sx={{
            overflow: 'hidden', // Hide overflow
            borderRight: '0.15em solid orange', // Cursor effect
            whiteSpace: 'nowrap', // Keep text in one line
            margin: '0 auto', // Center the text
            letterSpacing: '0.15em', // Spacing between letters
          }}
        >
          {text}
        </Typography>

        {/* Text Field */}
        <TextField
          label="Your app name"
          variant="outlined"
          value={appName}
          onChange={(e) => setAppName(e.target.value)} // Update app name state
          sx={{ width: '300px', mb: 2 }}
        />

        {/* Start Button */}
        <Button
          variant="contained"
          color="primary"
          size="large"
          onClick={handleStart} // Handle button click
        >
          Start
        </Button>
      </Box>
    </Layout>
  );
};

export default IndexPage;